import infoText from '../../../components/m-info-text/m-info-text'
export default {
	data() {
		return {
			buy: true,
			take: false,
			role: [],
			statusAll: '',
			userCode: ''
		}
	},
	onLoad(option) {
		this.getJurisdiction()
		this.userCode = option.userCode;

	},
	methods: {
		getJurisdiction() {
			this.test.post('user/list_user_role').then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.role = res.data.value;
					console.log(this.role)
					// if (this.role.length == 2) {
					// 	console.log(1)
					// 	this.buy = true;
					// 	this.take = true;
					// } else if (this.role.length == 1) {
					// 	if (this.role[0].role_id == 1) {
					// 		console.log(2)
					// 		this.buy = true
					// 		this.take = false
					// 	} else if (this.role[0].role_id == 2) {
					// 		console.log(3)
					// 		this.take = true
					// 		this.buy = false
					// 	}
					// }
				} else if(res.data.errorCode == 10001 || res.data.errorCode == 10002 || res.data.errorCode == 10003){
					uni.showModal({
						title: '提示',
						content: res.data.message,
						success: function(res) {
							if (res.confirm) {
								uni.reLaunch({
									url: '../login/login'
								})
							} else {
								uni.reLaunch({
									url: '../login/login'
								})
							}
						}
					})
				}else{
					uni.showToast({
						title: res.data.message,
						icon: "none"
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},



		GoBuyApply(stu, name, ind) {
			if (stu == 0 || stu == 2) {
				uni.navigateTo({
					url: './uploadImg/uploadImg?name=' + "apply" + '&user=' + name + '&userCode=' + this.role[ind].code
				})
			} else if (stu == 1) {
				uni.showToast({
					"title": "您已有权限",
					icon: 'none'
				})
			} else if (stu == -1) {
				uni.showToast({
					"title": "审核中...",
					icon: 'none'
				})
			} else {
				uni.showToast({
					"title": "审核未通过",
					icon: 'none'
				})
			}
		},
	},
	components: {
		infoText
	}
}
